export default {
    addButtonText: {
        type    : String,
        required: false,
        default : 'Tambah Data'
    },
    dataMap: {
        type    : Function,
        required: false,
        default: function (item) {
            return item;
        }
    },
    fields: {
        type    : [Array, Object],
        required: false
    },
    filter: {
        type    : String,
        required: false,
    },
    form: {
        type    : Form,
        default : () => new Form({}),
        required: false
    },
    modalSize: {
        type    : String,
        required: false,
        default : 'md'
    },
    noAction: {
        type    : Boolean,
        default : false
    },
    noAddButtonText: {
        type    : Boolean,
        default : false,
        required: false,
    },
    noDelete: {
        type    : Boolean,
        default : false
    },
    noEdit: {
        type    : Boolean,
        default : false
    },
    noIndex: {
        type    : Boolean,
        default : false
    },
    onDoubleClicked: {
        type    : Function,
        required: false,
        default: function (item) {
            return item;
        }
    },
    options: {
        type    : Object,
        default : () => ({}),
        required: false
    },
    params: {
        type    : Object,
        default : () => ({}),
        required: false
    },
    sortBy: {
        type    : String,
        required: false
    },
    title: {
        type    : String,
        required: false
    },
    url: {
        type    : String,
        required: true
    },
}


