<template>
    <div class="DataTable">
        <div class="row">
            <div style="min-width:182px;" class="mb-3 col-md-2 col-xs-12">
                <data-table-row-limitter v-model="meta.per_page"></data-table-row-limitter>
            </div>
            <div class="mb-3 col">
                <data-table-filter @change="doSearch" v-bind:value="search"></data-table-filter>
            </div>
            <div class="mb-3 col d-flex col-md-auto col-lg-auto">
                <slot name="before-top-button"></slot>
                <div v-if="!noAddButtonText" class="ml-auto">
                    <button class="btn btn-primary" v-on:click.prevent="create"> {{ addButtonText }} </button>
                </div>
            </div>
        </div>
        <b-table ref="table" :busy.sync="is_busy" :items="provider"
            v-bind="tableOptions"
            v-click-outside="clearSelected"
            v-model="items"
            v-on:row-dblclicked="onDoubleClick"
            v-on:row-clicked="toggleSelected"
            >
            <template slot="dt-index" slot-scope="{ index }" v-if="!noIndex">
                {{ index+1 + ((meta.current_page-1) * meta.per_page) }}
            </template>

            <template v-for="sslot in scopedSlots" :slot='sslot' slot-scope="data">
                <slot v-bind="data" :name="sslot" :meta="meta">
                    {{ data.value }}
                </slot>
            </template>

            <template slot="dt-action" slot-scope="data">
                <button v-if="!noEdit" v-on:click="edit(data.item)" v-bind:disabled="data.item.__editable == false" class="btn btn-success">
                    <i class="fa fa-pencil"></i>
                </button>

                <button v-if="!noDelete" v-on:click="destroy(data.item)" v-bind:disabled="data.item.__deletable == false" class="btn btn-danger">
                    <i class="fa fa-trash"></i>
                </button>
            </template>

            <template slot="table-caption"> Menampilkan {{ items.length }} dari {{ meta.total }} data </template>
        </b-table>

        <b-pagination :per-page="meta.per_page" :total-rows="meta.total" v-model="meta.current_page"></b-pagination>

        <loading-overlay v-show='is_busy'></loading-overlay>

        <form-modal ok-title="Hapus" ok-variant="danger" ref="delete" v-bind:form="form" v-bind:title="title">
            <template>
                Peringatan! Data yang dihapus tidak dapat dikembalikan kembali
            </template>
        </form-modal>

        <form-modal ok-title="Simpan" ref="form" v-bind:form="form" v-bind:size="modalSize" v-bind:title="title">
            <slot name="form"></slot>
        </form-modal>
    </div>
</template>

<script>
import bPagination from 'bootstrap-vue/es/components/pagination/pagination';
import bTable from 'bootstrap-vue/es/components/table/table';
import DataTableFilter       from './DataTableFilter.js';
import DataTableRowLimitter  from './DataTableRowLimitter.js';

import directives   from './directives';
import props        from './props';
import Form         from '../../form';
import debounce     from 'lodash.debounce';

export default {
    components: {
        bPagination,
        bTable,
        DataTableFilter,
        DataTableRowLimitter
    },
    directives,
    props,
    computed: {
        tableOptions() {
            return Object.assign({
                apiUrl     : this.url,
                bordered   : true,
                fields     : this.tableFields,
                filter     : this.search,
                hover      : true,
                sortBy     : this.sortBy,
                sortDesc   : false,
                striped    : false,
            }, this.options, {
                currentPage: Number.parseInt(this.meta.current_page),
                perPage    : Number.parseInt(this.meta.per_page),
            });
        },
        tableFields() {
            let fields = this.fields;

            if (!this.noIndex) {
                fields.unshift({
                    class  : 'text-right',
                    key    : 'dt-index',
                    label  : 'No',
                    thStyle: 'width:24px'
                });
            }

            if (!this.noAction) {
                fields.push({
                    class  : 'text-center',
                    label  : 'Aksi',
                    key    : 'dt-action',
                    thStyle: 'width:150px'
                });
            }

            return fields;
        },
        hasForm() {
            return (this.$slots.form || this.$scopedSlots.form) && this.form;
        },
        scopedSlots() {
            return Object.keys(this.$scopedSlots);
        }
    },
    data() {
        return {
            is_busy: false,
            items  : [],
            meta   : {
                current_page: 1,
                from        : 0,
                per_page    : 5,
                to          : 0,
                total       : 0,
            },
            search  : this.filter,
            selected: null
        }
    },
    methods: {
        provider(ctx) {
            return axios.get(ctx.apiUrl, {
                params: Object.assign({
                    limit  : ctx.perPage,
                    page   : ctx.currentPage,
                    search : ctx.filter,
                    sortBy : [
                        ctx.sortBy,
                        ctx.sortDesc ? 'desc': 'asc'
                    ],
                }, this.params)
            })
            .takeAtLeast(500)
            .then(response => {
                if (response.data.meta) {
                    this.meta.current_page = Number.parseInt(response.data.meta.current_page);
                    this.meta.from         = Number.parseInt(response.data.meta.from);
                    this.meta.to           = Number.parseInt(response.data.meta.to);
                    this.meta.per_page     = Number.parseInt(response.data.meta.per_page);
                    this.meta.total        = Number.parseInt(response.data.meta.total);
                }

                return response.data.data.map(this.dataMap) || [];
            })
            .catch(error => {
                if (error.response.status === 401) {
                    window.location.reload();
                }

                return [];
            });
        },
        refresh() {
            this.$refs.table.refresh();
        },
        doSearch: debounce(function (search) {
            this.$emit('update:filter', search);

            this.search = search;
        }, 300),
        onDoubleClick(item, row, event) {
            this.$emit('dt:row-double-click', item, row, event);

            this.$emit('input', item);

            this.toggleSelected(item);

            this.onDoubleClicked(item, row, event);
        },
        toggleSelected(item, row) {
            let rows = this.$refs.table.$el.querySelectorAll('tbody tr');

            rows.forEach(function (child, index) {
                if (index == row) {
                    child.classList.add('active');
                } else {
                    child.classList.remove('active');
                }
            });

            this.$set(this, 'selected', item);
        },
        create() {
            if (!this.hasForm) {
                return;
            }

            this.$emit('dt:item-create');

            this.$refs.form.post(this.url)
                .then(response => this.response(response))
                .catch(error => this.exception(error));
        },
        destroy(item) {
            this.form.assign(item);

            this.$refs.delete.delete(item.path)
                .then(response => this.response(response))
                .catch(error => this.exception(error));

            this.$emit('dt:item-destroy', item);
        },
        edit(item) {
            if (!this.hasForm) {
                return
            }

            this.form.assign(item);

            this.$refs.form.put(item.path)
                .then(response => this.response(response))
                .catch(error => this.exception(error));

            this.$emit('dt:item-edit', item);
        },
        response({ data }) {
            if (data.message && data.status) {
                window.flash(data.message, data.status);
            }

            this.refresh();
        },
        exception(error) {
            let response = error.response
            let data     = error.response.data;

            switch(true) {
                case data.message:
                    window.flash(data.message, 'error', 10000);
                    break;
                case response.status < 500:
                    window.flash(response.statusText, 'error', 10000);
                    break;
                default:
                    window.flash('Something went wrong!', 'error', 10000);
                    break;
            }

        },
        clearSelected() {
            this.selected = null;

            let rows = this.$refs.table.$el.querySelectorAll('tbody tr');

            rows.forEach(function (child, index) {
                child.classList.remove('active');
            });
        }
    },
    created() {
        this.meta.current_page = this.options.currentPage || 1;
        this.meta.per_page     = this.options.perPage     || 5;
    },
    watch: {
        filter(value) {
            this.search = value;
        },
        params: {
            handler: function (options) {
                this.refresh()
            },
        }
    },
}
</script>

<style lang="scss">
    .DataTable {
        position: relative;

        tbody .active {
            background-color: #f0f3f5 !important;
        }

        .DataTable__limit {
            display: flex;

            label {
                line-height: 35px;
                margin     : 0 1em 0 0;
                width      : 9em;
            }
        }
    }
</style>


